<style lang="stylus">
.js-menu
    float: left
    width: 220px
    color: #4c4c4c
    background: #fff
    padding: 0 10px
	.menu-option
		padding: 20px 0 5px 0
		border-bottom:1px solid #f2f2f5
	.menu-option-tit
		font-weight: bold
	.menu-option-ul
		margin:15px 0 0 0
		width: 208px
	.menu-option-ul li a
		font-size: 12px
	.menu-option-ul li
		width: 47%
		float:left
		padding: 5px 15px
		margin: 0 3% 15px 0
		text-align: center
		background: #f4f4f4
		cursor: pointer
	.menu-option-ul li.active 
		background: #3089dc
		a
			color: #fff
	.menu-option-color
		width: 210px;
		margin: 15px 0 0 0
		li
			width:30px
			height:22px
			overflow: hidden
			float: left
			border-radius: 4px
			margin: 0 12px 15px 0

		.red
			background: #ff0000
		.whiteBlack
			background: url(../../static/images/whiteBlack.png) no-repeat
			background-size: 100% 100%
		.black
			background: #000
		.greed
			background: #00ff00
		.blue
			background: #0000ff
		.pink
			background: #ffc0cb
		.brown
			background: #A67D3D
		.gray
			background: #808080
		.color
			background: url(../../static/images/color.png) no-repeat
			background-size: 100% 100%
		.purple
			background: #808080
		.orange
			background: #ffa500
		.yellow
			background: #FFFF00;
</style>

<template>
	<div class="js-menu">
		<div class="menu-option">
			<h3 class="menu-option-tit">模板行业</h3>
			<ul class="fix menu-option-ul">
				<li class="active">
					<a>女装</a>
				</li>
				<li>
					<a>鞋类</a>
				</li>
				<li>
					<a>五金机械</a>
				</li>
				<li>
					<a>工艺礼品</a>
				</li>
			</ul>
		</div>
		<div class="menu-option">
			<h3 class="menu-option-tit">色系分类</h3>
			<ul class="fix menu-option-color">
				<li class="red"></li>
				<li class="black"></li>
				<li class="whiteBlack"></li>
				<li class="greed"></li>
				<li class="blue"></li>
				<li class="pink"></li>
				<li class="brown"></li>
				<li class="gray"></li>
				<li class="color"></li>
				<li class="purple"></li>
				<li class="orange"></li>
				<li class="yellow"></li>
			</ul>
		</div>
	</div>
</template>
<script>
export default {
	name: 'Header',
	data() {
		return {

		}
	}
}
</script>



